
.futured-products{
  padding: 150px 0;
    &__title{

      margin-bottom: 90px;


    }
    &__buttons{
      display: flex;
      justify-content: center;
      border-bottom: 1px solid #fff;
      width: max-content;
      margin: auto;
      padding-bottom: 5px;
      margin-bottom: 90px;
    }
    &__button{
      background: transparent;
      border: none;
      color:#fff;
      font-family: var(--Poppins);
      font-size: 24px;
      cursor: pointer;
      position: relative;
      padding: 0;
      transition: all .3s;
      &:not(:last-child){
        margin-right: 70px;

      }
      &:after{
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background: var(--main);
        position: absolute;
        bottom: -6px;
        opacity: 0;
        transition: all .3s;
      }

      &.active{
        color:var(--main);
       &:after{
         opacity: 1;
       }
      }
    }
    &__item{

      text-align: center;
    }
    &__link{
      display: block;
      width: max-content;
      margin: auto;
      color:var(--main);
      font-size: 18px;
      transition: all .3s;

    }
}
.futured-item{
  width: 25%;
  margin-bottom: 90px;
  &__discount{
    position: absolute;
    top: 20px;
    left: 30px;
    font-family: var(--Poppins);
    color:var(--black);
    background: var(--main);
    padding: 10px 16px;
    font-size: 14px;
  }
  &__img{
    margin-bottom: 5px;
    min-height: 360px;
  }
  &__name{
    font-size: 24px;
    margin-bottom: 10px;
  }
  &__price{
    font-family: var(--Poppins);
    color:var(--main);
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
      span{
        font-size: 16px;
        display: inline-block;
        margin-right: 5px;
      }
  }
}